'use client';

import { useCallback, useEffect, useMemo, useState } from 'react';
import { useAccount, useReadContract, usePublicClient } from 'wagmi';
import { ConnectButton } from '@rainbow-me/rainbowkit';
import { sdk } from '@farcaster/miniapp-sdk';
import { COLLECTION_ABI } from '@/lib/abi/collection';
import { ERC721_EVENTS } from '@/lib/abi/erc721Events';
import { CONTRACT_ADDRESS } from '@/lib/constants';
import { ipfsToHttp } from '@/lib/ipfs';

type MintItem = {
  tokenId: bigint;
  metadataUrl: string;
  imageUrl?: string;
  name?: string;
  error?: string;
};

const BASE_URI = 'ipfs://bafybeif4nhbmbypovzvr2wz5p2kfm2wy2simfbbxawmkk2j3cjp4guxsgy/';

// بوابات IPFS بديلة لفشل/ipfs.io
const IPFS_GATEWAYS = [
  (hash: string) => `https://ipfs.io/ipfs/${hash}`,
  (hash: string) => `https://cloudflare-ipfs.com/ipfs/${hash}`,
  (hash: string) => `https://gateway.pinata.cloud/ipfs/${hash}`,
];

async function fetchWithFallback(urls: string[]) {
  for (const u of urls) {
    try {
      const res = await fetch(u, { cache: 'no-store' });
      if (res.ok) return await res.json();
    } catch {}
  }
  throw new Error('All gateways failed');
}

export default function MyNftsPage() {
  const { address, isConnected } = useAccount();
  const publicClient = usePublicClient();

  // Ready call لفاركاستر
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const appUrl = process.env.NEXT_PUBLIC_APP_URL;
    const sameOrigin = !appUrl || window.location.origin === appUrl;
    if (sameOrigin) {
      sdk.actions.ready().catch(console.error);
    }
  }, []);

  // لعرض العدد الذي سككته (اختياري معلوماتي)
  const { data: mintedOfMe } = useReadContract({
    address: CONTRACT_ADDRESS,
    abi: COLLECTION_ABI,
    functionName: 'mintedOf',
    args: [address ?? '0x0000000000000000000000000000000000000000'],
  });

  const [items, setItems] = useState<MintItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [logError, setLogError] = useState<string | null>(null);

  const mintedCount = useMemo(
    () => (mintedOfMe as bigint | undefined)?.toString() ?? (isConnected ? '0' : '—'),
    [mintedOfMe, isConnected]
  );

  const load = useCallback(async () => {
    if (!isConnected || !address || !publicClient) return;
    setLoading(true);
    setLogError(null);
    try {
      // fromBlock: خذه من env إن وُجد لتقليل المدى، وإلا ارجع للبداية
      const envBlock = process.env.NEXT_PUBLIC_DEPLOY_BLOCK;
      const fromBlock = envBlock ? BigInt(envBlock) : 0n;

      // 1) لوجات Transfer حيث to=address
      const logs = await publicClient.getLogs({
        address: CONTRACT_ADDRESS,
        event: ERC721_EVENTS.Transfer as any,
        // @ts-ignore viem يقبل args بالفلاتر المفهرسة
        args: { to: address },
        fromBlock,
        toBlock: 'latest',
      });

      // 2) استخرج tokenIds الفريدة
      const tokenIds = Array.from(
        new Set(logs.map((l: any) => l.args?.tokenId as bigint).filter(Boolean))
      );

      if (tokenIds.length === 0) {
        setItems([]);
        setLoading(false);
        return;
      }

      // 3) رشّح بالملكية الحالية ownerOf == address (لتظهر فقط المملوكة الآن)
      const owners = await Promise.all(
        tokenIds.map((tid) =>
          publicClient
            .readContract({
              address: CONTRACT_ADDRESS,
              abi: [
                {
                  type: 'function',
                  name: 'ownerOf',
                  stateMutability: 'view',
                  inputs: [{ name: 'tokenId', type: 'uint256' }],
                  outputs: [{ type: 'address' }],
                },
              ] as const,
              functionName: 'ownerOf',
              args: [tid],
            })
            .catch(() => null)
        )
      );
      const ownedNow = tokenIds.filter((_, i) => owners[i]?.toLowerCase() === address.toLowerCase());

      // 4) ابنِ روابط الميتاداتا والتحميل مع بوابات احتياطية
      const withMeta = await Promise.all(
        ownedNow.map(async (tid) => {
          const ipfsPath = `${BASE_URI.replace('ipfs://', '')}${tid}.json`;
          const urls = IPFS_GATEWAYS.map((fn) => fn(ipfsPath));
          try {
            const json = await fetchWithFallback(urls);
            const imageIpfs = json.image || json.image_url || '';
            const imageUrl = imageIpfs
              ? (imageIpfs.startsWith('ipfs://')
                  ? IPFS_GATEWAYS[0](imageIpfs.replace('ipfs://', ''))
                  : imageIpfs)
              : undefined;
            return {
              tokenId: tid,
              metadataUrl: urls[0],
              imageUrl,
              name: json.name,
            } as MintItem;
          } catch (e: any) {
            return {
              tokenId: tid,
              metadataUrl: urls[0],
              error: `Metadata failed: ${e?.message || e}`,
            } as MintItem;
          }
        })
      );

      setItems(withMeta);
    } catch (e: any) {
      setLogError(e?.message || String(e));
    } finally {
      setLoading(false);
    }
  }, [isConnected, address, publicClient]);

  // تحميل عند الدخول
  useEffect(() => {
    load();
  }, [load]);

  return (
    <main className="min-h-screen bg-neutral-950 text-white">
      <header className="flex items-center justify-between p-4 border-b border-neutral-800">
        <div className="text-lg font-semibold">RACCOON-MINT — My NFT</div>
        <div className="flex items-center gap-3">
          <button
            onClick={load}
            className="text-sm rounded-lg border border-neutral-700 px-3 py-1 hover:bg-neutral-900"
          >
            Refresh
          </button>
          <ConnectButton />
        </div>
      </header>

      <section className="max-w-3xl mx-auto p-6 grid gap-6">
        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-6">
          <h2 className="text-xl font-bold mb-3">Your Minted NFTs (Owned now)</h2>
          {!isConnected ? (
            <p className="text-sm text-neutral-300">Connect your wallet to see your NFTs.</p>
          ) : (
            <p className="text-sm text-neutral-300">
              Minted by you (lifetime): <span className="font-mono text-white">{mintedCount}</span>
            </p>
          )}
          {logError && <p className="mt-2 text-amber-300 text-sm">⚠️ {logError}</p>}
        </div>

        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-4">
          {loading ? (
            <p className="text-sm text-neutral-300">Loading your NFTs…</p>
          ) : items.length === 0 ? (
            <p className="text-sm text-neutral-300">
              {isConnected ? 'No NFTs owned found for this wallet.' : 'Connect wallet to view.'}
            </p>
          ) : (
            <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
              {items.map((it) => (
                <article key={it.tokenId.toString()} className="rounded-xl overflow-hidden border border-neutral-800 bg-neutral-900">
                  <div className="aspect-square bg-neutral-800 flex items-center justify-center">
                    {it.imageUrl ? (
                      // eslint-disable-next-line @next/next/no-img-element
                      <img src={it.imageUrl} alt={it.name ?? `#${it.tokenId.toString()}`} className="w-full h-full object-cover" />
                    ) : (
                      <span className="text-xs text-neutral-400 p-2 text-center">
                        {it.error ? it.error : 'No image'}
                      </span>
                    )}
                  </div>
                  <div className="p-3 text-sm">
                    <div className="font-mono">#{it.tokenId.toString()}</div>
                    {it.name && <div className="text-neutral-300">{it.name}</div>}
                    <a
                      className="text-cyan-300 text-xs hover:underline"
                      href={IPFS_GATEWAYS[0](`${BASE_URI.replace('ipfs://', '')}${it.tokenId}.json`)}
                      target="_blank"
                      rel="noreferrer"
                    >
                      Metadata →
                    </a>
                  </div>
                </article>
              ))}
            </div>
          )}
        </div>

        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-6">
          <a
            className="text-cyan-300 hover:underline"
            href={`https://basescan.org/address/${CONTRACT_ADDRESS}`}
            target="_blank"
            rel="noreferrer"
          >
            View contract on BaseScan →
          </a>
        </div>
      </section>
    </main>
  );
}
